.footer {
    font-family: "BIZ UDPGothic", sans-serif;
    color: black;
}

p {
    font-family: "BIZ UDPGothic", sans-serif;
}

.someRowName {
    font-size: 1.75rem;
    font-family: "BIZ UDPGothic", sans-serif;
    text-shadow: 0.3rem 0.3rem 0.2rem rgba(84, 84, 84, 0.39);
}

.someRow {
    background-color: rgb(191, 224, 22);
    display: flex;
    justify-content: space-between;
    padding: 0.25rem 2.25rem 0.25rem 2.25rem;
    font-family: "BIZ UDPGothic", sans-serif;
    align-items: center;
    box-shadow: -0.5rem -0.5rem 1rem rgba(0, 0, 0, 0.11);
}

.someBtns {
    align-items: center;
    display: flex;
}


.SclsImg{
    height: 2rem;
    margin: 0.1rem 0 0.1rem 1.5rem; 
 }

.pystyRivit {
    padding: 1.5rem 0;
    background-color: rgb(201, 238, 21);
    display: flex;
    justify-content: space-around;
    font-size: 1.4rem;
    font-family: "BIZ UDPGothic", sans-serif;
}

.footerLogo {
    object-fit: contain;
    height: 4.5rem;
}

.footerName {
    font-weight: 700;
}

.footerAdrs {
    color: rgb(73, 73, 73);
}

.rowHead {
    margin-bottom: 0.75rem;
    font-family: "BIZ UDPGothic", sans-serif;
    text-decoration: underline;
    text-underline-offset: 0.25rem;
}

.footerNav {
    display: grid;
}

.Link {
    color: black;
}

.Link:hover {
    cursor: pointer;
    text-decoration: underline;
    text-underline-offset: 0.25rem;
}

.rights {
    display: flex;
    justify-content: space-between;
    padding: 1rem 3rem 0.5rem 3rem;
    background-color: rgba(202, 238, 21, 0.692);
    font-family: "BIZ UDPGothic", sans-serif;
}

.rightsTxt {
    color: black;
}

@media only screen and (max-width: 630px) {
    .someRow {
      flex-direction: column;
      align-items: center;
    }
  
    .someBtns {
      margin-top: 1rem;
    }
  
    .pystyRivit {
      flex-direction: column;
      align-items: center;
    }
  
    .rights {
      flex-direction: column;
      align-items: center;
    }
  
    .rightsTxt {
      margin-bottom: 1rem;
    }

    .frow {
        margin-bottom: 1.25rem;
    }
    .footerNav {
        margin-bottom: 1.25rem;
    }
    .srow {
        margin-left: 0;
    }
  }
  